@media only screen and (max-width: 1440px) and (min-width: 1291px) {
  .blob {
    width: 45%;
    height: 74%;
  }
}
@media only screen and (max-width: 1290px) and (min-width: 1051px) {
  .title-text {
    top: 1.15em;
    font-size: 25vh;
  }
  #tribute__name {
    font-size: 43.1vh;
  }
  .blob {
    top: 17%;
    width: 50vw;
    height: 50vw;
  }
  .e5,
  .e6 {
    width: 28%;
  }
  .text {
    font-size: 3.5em;
  }
}
@media only screen and (max-width: 720px) {
  .blob {
    width: 600px;
    height: 600px;
  }
  #scrollDown {
    display: block;
  }
}
@media only screen and (max-width: 1050px) and (min-width: 651px) {
  .blob {
    top: 27%;
    width: 500px;
    height: 500px;
  }
  .text {
    font-size: 3.5em;
  }
  .e4 {
    left: 35%;
  }
}
@media only screen and (max-width: 650px) {
  .e5,
  .e6 {
    display: none;
  }
  .title-text {
    top: 1.9em;
    font-size: 18vh;
  }
  #tribute__name {
    font-size: 43.1vh;
  }
}
@media only screen and (max-width: 650px) and (min-width: 481px) {
  .blob {
    top: 20%;
    width: 60%;
    height: 90%;
  }
  .text {
    font-size: 2.5em;
  }
}
@media only screen and (max-width: 420px) {
  .blob {
    display: none;
  }
  .text {
    font-size: 1.7em;
  }
  .e1 {
    left: 10%;
  }
  .e1,
  .e2,
  .e3,
  .e4,
  .e5,
  .e6 {
    width: 25%;
    position: absolute;
  }
}
