@keyframes floatEmojis {
  0%,
  to {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
}
@keyframes rotateEmojis {
  0%,
  to {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(10deg);
  }
}
.banner__tribute {
  background: -webkit-linear-gradient(0deg, #0e72ab 0, #005189 40%);
  background: linear-gradient(0deg, #0e72ab 0, #005189 40%);
  background-color: var(--bg__color);
  min-height: 100vh;
  z-index: 1;
  position: relative;
}
.banner__content,
.content__title {
  display: flex;
  justify-content: center;
  align-items: center;
}
.banner__content {
  flex-flow: column wrap;
  min-height: 100%;
}
.content__title {
  flex-flow: column-reverse wrap;
}
.title-text {
  position: absolute;
  top: 1.15em;
  color: var(--subtitle__color);
  font-family: morganita;
  text-align: center;
  font-size: 25vh;
  z-index: 2;
}
#tribute__name {
  top: 1em;
  text-shadow: 0 -7px 10px var(--shadow__color);
  color: var(--title__color);
  font-size: 43.1vh;
  text-transform: uppercase;
  z-index: 3;
}
.blob {
  position: absolute;
  top: 20%;
  width: 40%;
  height: 74%;
  z-index: 1;
}
.e1 {
  width: 20%;
}
.e1,
.e2 {
  position: absolute;
}
.e3 {
  width: 20%;
}
.e3,
.e4,
.e5,
.e6 {
  position: absolute;
}
.e1 {
  animation: floatEmojis 2s infinite ease-in-out;
  right: 75%;
  top: 20%;
}
.e2 {
  animation: floatEmojis 4s infinite ease-in-out;
  width: 15%;
  left: 70%;
  top: 60%;
}
.e3 {
  animation: floatEmojis 5s infinite ease-in-out;
  right: 15%;
  top: 10%;
}
.e4 {
  animation: floatEmojis 7s infinite ease-in-out;
  width: 10%;
  left: 20%;
  bottom: 10%;
}
.e5,
.e6 {
  opacity: 0;
  width: 25%;
  top: 40%;
  animation: rotateEmojis 1s infinite ease-in-out;
}
.e5 {
  right: 10%;
  rotate: 15deg;
}
.e6 {
  left: 22%;
  rotate: -90deg;
}

#scrollDown {
  position: absolute;
  bottom: 2%;
  display: none;
}
